@import "../partials/variables";

//Style 06
#atw_style6{
    .atw__title6{
        background:#f5f5f5;
        padding: 15px 0;
        margin-bottom: 60px;
        &:before{
            content: none;
        }
    }
    .atw--single_item{
        &:hover{
            .atw_item_top{
                .atw_post_view{
                    top: 50%;
                    visibility: visible;
                    opacity: 1;
                }
            }
        }
        .atw_item_top{
            .atw_post_view{
                border-radius: 50%;
                position: absolute;
                left: 50%;
                top: 60%;
                transform: translate(-50%, -50%);
                height: 40px;
                width: 40px;
                font-size: 14px;
                color: #fff;
                text-align: center;
                background: $theme1;
                padding-top: 13px;
                visibility: hidden;
                opacity: 0;
                transition: $transition;
            }
        }
        .atw_item_bottom{
            padding-top: 20px;
            text-align: center;
            .atw_item_title{
                font-weight: 400;
                a{
                    color: $titleColor;
                    font-size: 16px;
                    line-height: 26px;
                    &:hover{
                        color: $theme1;
                    }
                }
            }
            .atw_item_info{
                margin-top: 8px;
                .atw_rating, .atw_price{
                    display: block;
                }
                .atw_price{
                    color: $theme1;
                    line-height: 44px;
                    del{
                        color: #9192a3;
                        font-weight: 300;
                        font-size: 14px;
                    }
                }
                .atw_btn{
                    padding: 0 20px;
                }
            }
        }
    }
    .atw_slider_controls{
        .slider--control6{
            border: 1px solid $borderColor;
            border-radius: 0;
            z-index: 2;
            position: absolute;
            top: 50%;
            transform: translateY(-50%);
            transition: $transition;
            &:hover{
                border-color: $theme1;
                color: $theme1;
            }
        }
        .icon-arrow-left{
            left: 0;
        }
        .icon-arrow-right{
            right: 0;
        }
    }
}